/* AboutPage.css */
.contact-page {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100svh;
    touch-action: none;
    -webkit-user-drag: none;
    user-select: none;
}

.about-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    touch-action: none;
    -webkit-user-drag: none;
    user-select: none;
  }
  
  .emoji-link {
    font-size: var(--Emoji-Font-Size); 
    margin: 0 10px; 
    text-decoration: none; 
  }
  
  .emoji-link:active {
    opacity: var(--Hover-Opacity);
  }
  

.biography {
    font-size: var(--Emoji-Font-Size); 
height: 150px;
}

a {
    color: black;
}