/* Filter.css */
.filter-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100svh;
  display: none;  /* Default to hidden; will display in specific media queries */
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  z-index: 2000;
  box-sizing: border-box;
}

/* Desktop specific styles */
.filter-overlay-desktop {
  display: flex; /* Visible on desktop */
}

/* Mobile specific styles */
.filter-overlay-mobile {
  display: flex; /* Hidden by default but shown in mobile styles */
}

/* Common styling for both versions */
.close-button {
  font-size: var(--Emoji-Font-Size);
  position: absolute;
  top: 10px;
  cursor: pointer;
  z-index: 1000;
  user-select: none;
}

.close-button:active {
  opacity: var(--Hover-Opacity);
}

.tag-grid-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100vh;
  width: 100vw;
  box-sizing: border-box;

}

.tag-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 90%;
  overflow-y: auto;
  padding-bottom: 200px;
  padding-top: 110px;
}

.tag-item {
  position: relative; /* Ensures the ::before element is positioned relative to the tag */
  font-size: var(--Emoji-Font-Size);
  cursor: pointer;
  border-radius: 8px;
  /* border: 2px solid transparent; */
  z-index: 1; /* Ensure the tag content is above the pseudo-element */
  padding: 10px; 
  display: inline-block; /* To ensure the tag item dimensions are respected */
}

.tag-item:active {
  opacity: 0.7;
}

.tag-item.selected::before {
  content: ''; /* Required for the ::before element */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100px; /* Ensure it's larger than the tag */
  height: 100px; /* Make the height and width equal for a perfect circle */
  background-color: rgba(255, 217, 0, 0.4); /* Glowing yellow background */
  border-radius: 50%; /* Makes it a perfect circle */
  box-shadow: 0 0 20px rgba(255, 217, 0, 0.7); /* Adds glowing effect */
  z-index: -1; /* Puts the glowing circle behind the tag */
}

.tag-item.selected {
  opacity: 1; /* Keep selected tags fully opaque */
}

.tag-item.inactive {
  /* opacity: 0.5; */
  /* pointer-events: none; */
}






.confirm-button {
  font-size: var(--Emoji-Font-Size);
  cursor: pointer;
  position: fixed;
  bottom: 20px;
  display: flex;
  text-align: center;
  justify-content: center;
  user-select: none;
}

.confirm-button:active {
  opacity: var(--Hover-Opacity);
}

.tag-query {
  font-size: 1.5rem;
  text-align: center;
  white-space: nowrap;
  font-size: var(--Emoji-Font-Size);
}

.query-tag {
  cursor: pointer;
}

.query-tag:active {
  opacity: 0.5;
}

.tag-item.inactive {
  opacity: 0.2;
  pointer-events: none;
}

.tag-item.selected:active {
  opacity: var(--Hover-Opacity);
}



/* Desktop styles */
@media (min-width: 769px) {
  .filter-overlay-desktop {
    display: flex; /* Show only on desktop */
  }

  .filter-overlay-mobile {
    display: none; /* Hide on desktop */
  }

  .tag-image {
    max-width: 100px;
    max-height: 80px;
    object-fit: cover; 
  }

  .tag-item.selected::before {
    content: ''; /* Required for the ::before element */
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100px; /* Ensure it's larger than the tag */
    height: 100px; /* Make the height and width equal for a perfect circle */
    background-color: rgba(255, 217, 0, 0.4); /* Glowing yellow background */
    border-radius: 50%; /* Makes it a perfect circle */
    box-shadow: 0 0 20px rgba(255, 217, 0, 0.7); /* Adds glowing effect */
    z-index: -1; /* Puts the glowing circle behind the tag */
  }

}

/* Mobile styles */
@media (max-width: 768px) {
  .filter-overlay-desktop {
    display: none; /* Hide on mobile */
  }

  .filter-overlay-mobile {
    display: flex; /* Show only on mobile */
  }

  .tag-image {
    max-width: 100px;
    max-height: 50px;
    object-fit: cover; 
  }

  .close-button {
    position: inherit;
  }

  .confirm-button {
    position: inherit;
  }

  .tag-grid-container {
    /* justify-content: normal; */
  }

  .tag-item.selected::before {
    width: 80px; /* Ensure it's larger than the tag */
    height: 80px; /* Make the height and width equal for a perfect circle */
  }
  .tag-item.selected::before {
    content: ''; /* Required for the ::before element */
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 60px; /* Ensure it's larger than the tag */
    height: 60px; /* Make the height and width equal for a perfect circle */
    background-color: rgba(255, 217, 0, 0.4); /* Glowing yellow background */
    border-radius: 50%; /* Makes it a perfect circle */
    box-shadow: 0 0 20px rgba(255, 217, 0, 0.7); /* Adds glowing effect */
    z-index: -1; /* Puts the glowing circle behind the tag */
  }

  .tag-item {
    padding: 8px; 
  }

}

.filter-buttons-mobile {
  font-size: var(--Emoji-Font-Size);
  cursor: pointer;
  position: fixed;
  bottom: 20px;
  display: flex;
  /* gap: 8px; */
  text-align: center;
  justify-content: center;
  z-index: 1000;
}


