html, body {
  margin: 0;
  padding: 0;
}

.slideshow {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; /* Center the content vertically */
  position: relative;
  height: 100dvh; /* Full dynamic viewport height */
  width: 100vw;  /* Full viewport width */
  box-sizing: border-box;
  overflow: hidden; /* Prevent scrolling inside the slideshow */
  touch-action: none;
  -webkit-user-drag: none;
  user-select: none;
}

.slideshow-image {
  display: flex;
  justify-content: center; /* Center the image horizontally */
  align-items: center; /* Center the image vertically */
  max-height: 65dvh; /* Set height to 65% of dynamic viewport height */
  width: 100%; /* Ensure it takes full width of the container */
  margin-bottom: 14px;
}

.slideshow-image img {
  min-height: 70dvh; /* Ensure the image fits within the container's height */
  max-width: 100%; /* Ensure the image scales based on its aspect ratio */
  object-fit: contain; /* Preserve the image aspect ratio */
  touch-action: none;
  -webkit-user-drag: none;
  user-select: none;
}

.arrows {
  position: fixed;
  bottom: 10px; /* Keep the arrows fixed at the bottom */
  display: flex;
  text-align: center;
  justify-content: space-around;
  gap: 10px;
}

.arrow {
  font-size: var(--Emoji-Font-Size); /* Use the Emoji Font Size variable */
  cursor: pointer;
}

.arrow:active {
  opacity: var(--Hover-Opacity);
}

.nav-left {
  cursor: w-resize;
}

.nav-right {
  cursor: e-resize;
}

@media (max-width: 768px) {
  .arrows {
    display: none;
  }
}
