/* Global Styles */
html, body {
    height: 100svh;
    margin: 0;
    padding: 0;
    overflow: auto; /* Ensure scrolling is allowed */
}

/* IndexView Component Styles */
.index-view-container {
    display: flex;
    justify-content: center;
    margin-top: 80px;
    padding-bottom: 50px; /* Adds white space below */
    /* min-height: calc(100vh - 80px); */ /* Removed for natural growth */
}

.index-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1px;
    max-width: 100%;
}

.index-image {
    width: 24vw; /* Default for small screens */
    height: auto;
    cursor: pointer;
}

.index-image:hover {
    opacity: var(--Hover-Opacity);
}

.index-image:active {
    opacity: 0.5;
}


/* Medium screen: tablet or small desktops */
@media (min-width: 768px) {
    .index-view-container {
        margin-top: 100px;
        /* Remove height */
    }
    .index-grid {
        grid-template-columns: repeat(4, 1fr); /* 4 columns for medium screens */
        gap: 4px;
    }
}

/* Large screens: desktops */
@media (min-width: 1024px) {
    .index-view-container {
        margin-top: 110px;
        /* Remove height */
    }
    .index-grid {
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr)); /* Dynamic columns, each at least 200px wide */
        gap: 20px;
        justify-content: center; /* Ensure the grid stays centered */
    }

    .index-image {
        width: 200px; /* Ensure the image width is fixed for large screens */
    }
}
