/* General Navbar Styles */
.navbar-container {
    width: 100%;
    z-index: 1000;
}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    width: 100%;
    box-sizing: border-box;
}

.nav-item {
    font-size: var(--Emoji-Font-Size);
    text-decoration: none;
    cursor: pointer;
}

/* Desktop Specific Styles */
.navbar-desktop {
    position: fixed;
    top: 0;
    left: 0;
}

/* Mobile Specific Styles */
.navbar-mobile {
    display: none;
    /* position: fixed;
    bottom: 0;
    left: 0; */
    position: fixed;
    top: 0;
    left: 0;
}

/* Filter section on mobile (top of the screen) */
.filter-section-mobile {
    display: none;
    position: fixed;
    text-align: center;
    /* top: 0; */
    left: 0;
    width: 100%;
    z-index: 1001;

    /* left: 10px; */
    bottom: 20px;
}

/* Show mobile nav on smaller screens */
@media (max-width: 768px) {
    .navbar-desktop {
        /* display: none; */
    }

    .navbar-mobile {
        /* Mobile nav currently disabled */
        /* display: flex; */
        display: none;
    }

    .filter-section-mobile {
        display: flex;
        justify-content: center;
        align-items: center;
        display: none;
    }

    #filter-nav {
        /* display: none; */
    }


    /* To make the mobile nav left aligned to make room for the slideshow buttons */
    /* .navbar {
        justify-content: left;
        gap: 30px;
    } */

}

/* Filter section (equation + X) */
.filter-section {
    display: flex;
    align-items: center;
    text-align: center;
}

.clear-filters {
    /* margin-left: 10px; */
    font-size: var(--Emoji-Font-Size);
    cursor: pointer;
}

.clear-filters:active,
#about-nav:active,
#filter-nav:active,
#index-nav:active,
.filters:active,
#mobile-filter-nav:active {
    opacity: var(--Hover-Opacity);
}


